<template>
  <button
    @click="setFastOrder(dataArray)"
    class="product-form__fast-order"
  >
    <slot />
  </button>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'FastOrder',
  props: {
    dataFastOrder: {
      type: String,
      required: true,
      default: ''
    }
  },
  computed: {
    dataArray () {
      const dataSet = this.dataFastOrder.split(',')
      const data = dataSet.map(item => item.trim())
      return [...data, '-1']
    }
  },
  methods: {
    ...mapActions(['setFastOrder'])
  }
}
</script>

<style lang="scss" scoped>
.product-form {
    &__fast-order {
        padding: 12.5px 27.1px;
        margin-left: 10px;
        color: #383838;
        background-color: #fbc000;
    }

    &__fast-order {
        border-radius: 3px;
        outline: none;
        border: none;
        font-size: 16px;
        cursor: pointer;

        @media (max-width: 767px) {
            font-size: 14px;
        }
    }
}
</style>
