<template>
  <form
    @click="close"
    class="city-modal"
  >
    <span
      v-if="!mobile"
      class="city-modal__mobile-heading"
    >
      <span
        @click.prevent="$emit('close-modal')"
        class="fas fa-chevron-left"
      /> Выбор города доставки
    </span>
    <h2
      v-if="mobile"
      class="city-modal__heading"
    >
      Ваш город доставки
    </h2>
    <field
      ref="locationCities"
      placeholder="Введите город доставки"
    />
    <div class="search-help">
      <button value="Москва" @click.prevent="locationSelect($event.target.value)">Москва</button>
      <button value="Санкт-Петербург" @click.prevent="locationSelect($event.target.value)">Санкт-Петербург</button>
    </div>
    <a
      v-if="mobile"
      class="city-modal__submit"
      :href="locationLink"
      :style="{ 'pointer-events': !locationLink ? 'none' : 'auto' }"
    >
      Да, верно
    </a>
    <a
      v-else
      class="city-modal__submit"
      :href="locationLink"
      :style="{ 'pointer-events': !locationLink ? 'none' : 'auto' }"
    >
      Готово
    </a>
    <i
      v-if="mobile"
      @click.prevent="$emit('close-modal')"
      class="icon-close"
    />
  </form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import Field from './Field.vue'
export default {
  name: 'LocationForm',
  props: {
    mobile: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  components: {
    Field
  },
  computed: {
    ...mapGetters(['locationLink'])
  },
  methods: {
    close (e) {
      const target = e.target
      if (target.classList.contains('city-modal')) {
        this.$refs.locationCities.open = false
      }
    },
    ...mapActions(['locationSelect'])
  }
}
</script>

<style lang="scss" scoped>
.search-help {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  width: 324px;

  button {
    padding: 4.5px 8px;
    margin-right: 10px;
    border: 1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    outline: none;
    background-color: #fff;
    color: #515151;
    font-size: 16px;
    cursor: pointer;
  }
}

.city-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 533px;
  padding: 47px 0 39px 0;

  &__heading {
    margin-top: 0;
    margin-bottom: 23px;
    text-align: center;
    color: #515151;
    font-size: 30px;
    font-weight: 400;
  }

  &__submit {
    margin-top: 32px;
    padding: 10px 50px;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    background-color: #8fb35b;
    cursor: pointer;
  }

  &__mobile-heading {
    position: relative;
    display: block;
    width: 100%;
    padding: 14px 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.22);
    background-color: #8fb35b;
    color: #fff;
    font-size: 14px;
    text-align: center;

    .fas {
      position: absolute;
      left: 19px;
      top: calc(50% - 13px/2);
      font-size: 13px;
    }
  }

  @media (max-width: 767px) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 0;
    padding-bottom: 10px;

    &__submit {
      margin-top: 31px;
    }
  }
}
</style>
