import subscribe from '../../api/subscribe'

const state = {
  text: 'За первую авторизацию в мобильном приложении',
  success: false
}

const getters = {}

const mutations = {
  setText (state, payload) {
    state.text = payload
  },
  setSuccess (state, payload) {
    state.success = payload
  }
}

const actions = {
  getLink ({ commit }, phone) {
    subscribe.getLink((text, success) => {
      commit('setText', text)
      commit('setSuccess', success)
    }, phone)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
