<template>
  <div
    class="city-modal__field search-field"
    :class="{ 'city-modal__field_drop': open }"
  >
    <input
      @input="open = true; locationSelect($event.target.value)"
      :value="location"
      type="text"
      :placeholder="placeholder"
      :disabled="disabled"
    >
    <ul
      class="city-list"
      :class="{ 'city-list_opened': open }"
    >
      <li
        v-for="(item, index) in inputFiltering"
        :key="index"
        @click="setValue(item.title)"
        class="city-list__item"
      >
        {{ item.title }}
      </li>
    </ul>
    <span
      @click.prevent="open = !open"
      class="fas fa-chevron-down"
      :class="{ up:open }"
      :style="{ 'pointer-events': !inputFiltering.length ? 'none' : 'auto' }"
    />
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters   } from 'vuex';

export default {
  name: 'Field',
  props: {
    placeholder: {
      type: String,
      required: true,
      default: ''
    }
  },
  data () {
    return {
      open: false,
      disabled: true
    }
  },
  computed: {
    ...mapState({
      location: state => state.location.location
    }),
    ...mapGetters(['inputFiltering'])
  },
  methods: {
    ...mapActions(['locationSelect', 'citiesList']),
    setValue (val) {
      this.locationSelect(val)
      this.open = false
    }
  },
  mounted() {
    this.citiesList().then((res) => {
      this.disabled = res
    })
  },
}
</script>

<style lang="scss" scoped>
.city-modal__field {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    width: 324px;
    height: 30px;
    margin-bottom: 21px;
    border: 1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    background-color: #fff;
    overflow: hidden;

    input {
      position: relative;
      width: 100%;
      height: 100%;
      padding: 0 21px;
      margin-right: -25px;
      line-height: 30px;
      color: #515151;
      font-size: 16px;
      border: none;
      outline: none;
      z-index: 1;
    }

    span {
      position: relative;
      z-index: 3;
      margin-right: 11px;
      color: #4f4f4f;
      font-size: 16px;
      cursor: pointer;
      transition: all ease 0.5s;
    }

    &_drop {
      overflow: visible;
    }

    @media (max-width: 767px) {
      width: calc(100% - 20px);
      height: 44px;
      margin: 31px 0;

      input {
        padding-left: 7px;
        font-size: 13px;
      }
    }
}

.city-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  list-style: none;
  padding: 7px 11px;
  margin: 0;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  border-radius: 0 0 3px 3px;
  border: 1px solid #ffffff;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  transition: all ease 0.3s;

  &__item {
    margin-bottom: 9px;
    color: #515151;
    font-size: 16px;
    cursor: pointer;
  }

  &_opened {
    opacity: 1;
    visibility: visible;
    z-index: 5;
  }
}

.up {
  transform: rotate(180deg)
}
</style>
