import Vue from 'vue'
import Cart from './app/cart/Cart.vue'
import CategoryButton from './app/categoryButton/CategoryButton.vue'
import Location from './app/location/Location.vue'
import SeoBlock from './app/seoBlock/SeoBlock.vue'
import SeoBlockTop from './app/seoBlockTop/SeoBlockTop.vue'
import FastOrder from './app/fastOrder/FastOrder.vue'
import CallbackModal from './app/callbackModal/CallbackModal.vue'
import SubscribePanel from './app/subscribePanel/SubscribePanel.vue'
import ProductFastOrder from './app/productFastOrder/ProductFastOrder.vue'
import Delivery from './app/delivery/Delivery.vue'
import store from './store'

Vue.config.productionTip = false

document.addEventListener('DOMContentLoaded', () => {
  if (document.querySelector('location-modal')) {
    new Vue({
      store,
      components: {
        'location-modal': Location
      }
    }).$mount('location-modal')
  }

  if (document.querySelector('product-cart')) {
    new Vue({
      store,
      components: {
        'product-cart': Cart
      }
    }).$mount('product-cart')
  }

  if (document.querySelector('callback-modal')) {
    new Vue({
      store,
      components: {
        'callback-modal': CallbackModal
      }
    }).$mount('callback-modal')
  }

  if (document.querySelector('subscribe-panel')) {
    new Vue({
      store,
      components: {
        'subscribe-panel': SubscribePanel
      }
    }).$mount('subscribe-panel')
  }

  if (document.querySelector('product-fast-order')) {
    new Vue({
      store,
      components: {
        'product-fast-order': ProductFastOrder
      }
    }).$mount('product-fast-order')
  }

  const buttons = [...document.querySelectorAll('.category-button, .product-form__to-cart, .category-list__item-button')]

  buttons.forEach(element => {
    const dataSet = element.dataset.add2basket.split(',')
    const data = dataSet.map(item => item.trim())
    new Vue({
      store,
      render: h => h(CategoryButton, {
        props: {
          dataArray: data
        }
      })
    }).$mount(`#${element.id}`)
  })

  const seo = [...document.querySelectorAll('seo-block')]

  seo.forEach(element => {
    new Vue({
      store,
      components: {
        'seo-block': SeoBlock
      }
    }).$mount(`#${element.id}`)
  })

  const seoTop = [...document.querySelectorAll('seo-block-top')]

  seoTop.forEach(element => {
    new Vue({
      store,
      components: {
        'seo-block-top': SeoBlockTop
      }
    }).$mount(`#${element.id}`)
  })

  const fastOrder = [...document.querySelectorAll('fast-order')]

  fastOrder.forEach(element => {
    new Vue({
      store,
      components: {
        'fast-order': FastOrder
      }
    }).$mount(`#${element.id}`)
  })

  if (document.querySelector('delivery-select')) {
    new Vue({
      store,
      components: {
        'delivery-select': Delivery
      }
    }).$mount('delivery-select')
  }
})
