import axios from 'axios'
import getCookie from '../utils/getCookie'

const url = window.spa_url

const cityId = getCookie('CatalogCityId') || getCookie('CatalogCityIdRR')

const http = axios.create({
  baseURL: url,
  withCredentials: true,
  headers: { 'X-Catalog-Region': cityId }
})

export default {
  async getCities (callback) {
    try {
      const response = await http.get(`${window.location.origin}/api/data/get-geo-data`)
      callback(response.data.data, response.data.success)
    } catch (error) {
      console.log(error)
    }
  }
}
