<template>
  <form class="subscribe-panel">
    <div class="subscribe-panel__description subscribe-panel__description_store">
      <span class="subscribe-panel__heading">Бонус 1 000 <i class="icon-ruble-2"></i> сразу </span>
      <p class="subscribe-panel__text" :class="{ 'subscribe-panel__text_green': success }">{{ text }}</p>
      <div class="subscribe-panel__store">
        <a href="https://play.google.com/store/apps/details?id=com.jufy.amf">
          <img src="~@/assets/images/App-Store.png" alt="App-Store">
        </a>
        <a href="https://goo.gl/Qz4n2p">
          <img src="~@/assets/images/Google-Play.png" alt="Google-Play">
        </a>
      </div>
    </div>
    <div class="subscribe-panel__submit">
      <input v-model="phone" type="tel" placeholder="Номер телефона..." class="subscribe-panel__field">
      <button @click.prevent="getLink(phone)" class="subscribe-panel__button">Получить ссылку</button>
    </div>
  </form>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  data() {
    return {
      phone: ''
    }
  },
  computed: {
    ...mapState({
      text: state => state.subscribe.text,
      success: state => state.subscribe.success
    }),
  },
  methods: {
    ...mapActions(['getLink'])
  }
}
</script>

<style lang="scss" scoped>
  .subscribe-panel__text_green {
    color: #7e9d51;
    font-size: 14px;
  }
</style>
