import location from '../../api/location'

const state = {
  location: '',
  list: []
}

const getters = {
  inputFiltering (state) {
    const regexp = new RegExp(state.location, 'i')
    return [...state.list.filter(item => regexp.test(item.title))]
  },
  locationLink (state, getters) {
    let url
    const locationObj = getters.inputFiltering.find(item => item.title === state.location)
    url = locationObj ? locationObj.url : ''
    return url
  }
}

const mutations = {
  setList (state, payload) {
    state.list = payload
  },
  setLocation (state, payload) {
    state.location = payload
  }
}

const actions = {
  citiesList ({ commit }) {
    return new Promise((resolve, reject) => {
      location.getCities((list) => {
        const listObj = Object.entries(list).map(([, value]) => {
          return value.cities
        })
        const listMerge = listObj.reduce((a, b) => {
          return [...a, ...b]
        })

        commit('setList', listMerge)
        if (listMerge) {
          resolve(false)
        } else {
          reject(list)
        }
      })
    })
  },
  locationSelect ({ commit }, val) {
    commit('setLocation', val)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
